// @flow
import _ from 'lodash'
import type { Screen } from '../types'

export function getTitle(screen: Screen): string {
  switch (screen.type) {
    case 'new':
      return ''
    case 'search':
      return ''
    case 'profile':
      return _.compact([screen.q, screen.tag]).join(' | ')
    default:
      return ''
  }
}



